import React from "react";
import { Link } from "react-router-dom/cjs/react-router-dom.min";

const Tab = ({ active }) => {
	const tabs = [
		{ label: "Home", icon: "Ho", href: "/" },
		{ label: "Search", icon: "Se", href: "/search" },
		{ label: "Followed", icon: "Fw", href: "/followed" }
	];

	return (
		<div className="hide-lg">
			<div className="tab-container">
				{tabs.map(item => (
					<TabItem data={item}
						key={item.icon}
						active={active}></TabItem>
				))}
			</div>
		</div>
	);
};

const TabItem = ({ data, active }) => {
	return (
		<Link
			to={data.href}
			className={`tab-item ${active === data.label ? "tab-active" : ""}`}
		>
			<p className="tab-icon">{data.icon}</p>
			<p className="tab-label">{data.label}</p>
		</Link>
	);
};

export default Tab;
