import React, { useState, useEffect } from "react";
import { getSearch } from "../request/request";
import { useParams } from "react-router-dom";
import GridWrap from "../components/GridWrap";
import Loading from "../components/Loading";
import AppBar from "../components/AppBar";
import Tab from "./Tab";

const Search = () => {
	const [data, setData] = useState([]);
	const [isLoading, setLoading] = useState(true);
	const [key, setKey] = useState()
	let { searchKey } = useParams();


	useEffect(() => {
		let storedSearchKey = sessionStorage.getItem('searchKey')
		if (storedSearchKey && !searchKey) {
			setKey(storedSearchKey)
		} else {
			setKey(searchKey)
		}
	}, [searchKey])

	useEffect(() => {
		if (key) {
			let storedSearchKey = sessionStorage.getItem('searchKey')
			let storedData = JSON.parse(sessionStorage.getItem('searchData'))
			setLoading(true)

			if (storedSearchKey && Array.isArray(storedData) && key === storedSearchKey) {
				setData(storedData)
				document.title = "results for: " + key;
				setLoading(false)
			} else if (key && key.trim().length > 0) {
				setLoading(true);
				console.log('triggered')
				sessionStorage.setItem('searchKey', key)
				getSearch(key)
					.then(res => {
						setData(res);
						sessionStorage.setItem('searchData', JSON.stringify(res))
						setLoading(false);
						document.title = "results for: " + key;
					})
					.catch(err => {
						console.log(err);
						setLoading(false);
					});
			}
		}


	}, [key]);

	return (
		<div>
			<div className="hide-lg">
				<AppBar></AppBar>
				<div className="spacer"></div>
			</div>
			{key && key.trim().length > 0 && (
				<div>
					<div className="ui container  pb-10">
						<h3>results for: {key}</h3>
					</div>

					{!isLoading && data && data.length === 0 && (
						<p className="text-center pt-10">There's no result</p>
					)}
					{isLoading ? (
						<Loading />
					) : (
							<div className="mb-40">
								<GridWrap data={data} />
							</div>
						)}
				</div>
			)}
			<Tab active="Search"></Tab>
		</div>
	);
};

export default Search;
